.graphs-toolbar {
    padding: 0 10px;
}
.graphs-toolbar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.graphs-toolbar > div {
    margin-right: 15px;
}

@media (max-width: 640px) {
    .graphs-toolbar > div {
        margin-top: 50px;
        max-width: 50%;
        margin-right: 0;
    }
    .graphs-toolbar > div > div {
        max-width: 90%;
    }
    .graphs-toolbar {
        -ms-flex-pack: justify;
            justify-content: space-between;
    }
}



